.div2{
    margin-right: 0px;
 }
 
 .div{
    padding-right: 0px;
 }
 .ul {
   list-style: none;
   margin: 0;
   padding: 0;
}

   .title {
     font-size: 10px;
     font-weight: bold;
     color: #999;
     margin-top: 15px;
     margin-bottom: 5px;
   }
   .li {
       display: flex;
       align-items: center;
       padding: 5px;
       cursor: pointer;
   }
   .li:hover{
       background-color: #ece8ff;
   }
   .icon {
       font-size: 18px;
       color: #7451f8;
   }
   .center{
       padding-left: 10px;
   }